@import url('https://fonts.googleapis.com/css2?family=Edu+TAS+Beginner:wght@700&family=Indie+Flower&family=Kanit&family=Lato:wght@400;700&family=Roboto&display=swap');

header {
    height: 100vh;
    background-image: linear-gradient(45deg, rgba(6, 157, 82, .7), rgba(9, 236, 122, .6)),url('../assets/images/trees.jpg');
    background-size: cover;
    display: flex;
}

.left {
    height: 100vh;
    width: 100vw;
}

.left h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-100%, -100%);
    color: white;
    font-family: 'Kanit', sans-serif;
    font-size: 5rem;
}

.right {
    clip-path: polygon(40% 0, 100% 0, 100% 100%, 0 100%);
    background-color: white;
    height: 100vh;
    width: 100%;
}

.welcome {
    position: absolute;
    top: 25%;
    left: 65%;
    font-family: 'Roboto';
    font-size: 2.5rem;
}

.welcome a {
    background-color:rgb(9, 236, 122);
    border-radius: 20px;
    color: white;
    padding: .5em 1em;
    cursor: pointer;
    margin-left: 50px;
    font-size: 1.5rem;
}

.welcome a:hover {
    color: rgb(9, 236, 122);
    background-color: white;
    outline: 3px solid rgb(9, 236, 122);
}

@media (max-width: 1200px) {
    header {
        display: block;
        height: 180vh;
    }

    .left h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    
    .right {
        display: block;
        position: relative;
        top: 2em;
        clip-path: none;
    }
    
    .welcome {
        position: static;
        padding: 1em;
    }
}