.presentation {
    height: 80vh;
    padding: 3em;
    position: relative;
}

.presentation h2 {
    background-image: linear-gradient(to right, rgba(6, 157, 82, .7), rgba(9, 236, 122, .6));
    background-clip: text;
    color: transparent;
}

.presentation p {
    width: 50%;
}

.woman {
    position: absolute;
    top: 2em;
    right: 2em;
    height: 80vh;
}

.rainforest {
    height: 40vh;
}

@media (max-width: 1200px) {
    .presentation img {
        display: none;
    }
    .presentation p {
        width: 100%;
    }
}